<template>
  <div class="wrap-g">
    <header>
      <img src="../../../static/img/home/logo.png" style="width: 30px;height: 30px;float:left;;padding: 5px;box-shadow: 5px 0px 5px -5px rgba(0,0,0,0.50);">
      <img src="../../../static/img/home/fenlei.png" style="width: 20px;height: 20px;padding: 10px;float: right;box-shadow: -5px 0px 5px -5px rgba(0,0,0,0.50);">

      <el-tabs v-model="activeName" @tab-click="handleClick" style="font-size: 12px">
        <el-tab-pane label="首页" name="/home"></el-tab-pane>
        <el-tab-pane label="视频" name="/video"></el-tab-pane>
        <el-tab-pane label="语音" name="/Voice"></el-tab-pane>
        <el-tab-pane label="友情服务" name="/friendship"></el-tab-pane>
        <el-tab-pane label="商城" name="/mall"></el-tab-pane>
        <el-tab-pane label="成为讲师" name="/Applicationlecturer"></el-tab-pane>
        <el-tab-pane label="活动专栏" name="/activity"></el-tab-pane>
      </el-tabs>
    </header>
    <div class="main" style="margin-top: 40px;">
      <transition name="fade" mode="in-out">
        <router-view ></router-view>
      </transition>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        activeName:"/home",
      }
    },
    methods:{
      handleClick(e){
        this.$router.push({ path: e.name});
          console.log(e.name)
      },

    },
    mounted(){
        this.activeName=this.$route.path;
    },
  }
</script>


<style  scoped>


</style>



